html {
  height: auto;
  scroll-behavior: smooth;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: var(--primary-font);
}

main {
  display: grid;
  justify-items: center;
}

/* Убираем внутренние отступы */
ul,
ol {
  padding: 0;
}

/* Убираем внешние отступы */
h1,
h2,
h3,
h4,
p,
ul,
ol,
li {
  margin: 0;
}

/* Удаляем стандартные стили списков*/
ul {
  list-style: none;
}

/* Убираем лишние стили ссылок */
a {
  text-decoration: none;
  color: inherit;
}

/* Общие стили картинок */
img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}

header {
  display: grid;
  justify-content: center;
  align-items: center;
}

button {
  cursor: pointer;
}

select option {
  margin: 5px;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding: 5px;
  width: calc(100vw - 20px);
  max-width: 320px;

  @include mq(tablet) {
    width: calc(100vw - 140px);
    max-width: 600px;
    padding: 15px;
  }
  @include mq(desktop) {
    max-width: 1100px;
    padding: 15px;
  }
}

.overflow-hidden {
  overflow: hidden;
  @include mq(desktop) {
    padding-right: 17px;
  }
}

.header-logo {
  width: 250px;
  height: 70px;
  @include mq(tablet) {
    width: 450px;
    height: 100px;
  }
  @include mq(desktop) {
    width: 450px;
  height: 100px;
  }
}
