.select-section h2 {
  font-size: 18px;
  margin-bottom: 10px;
  @include mq(tablet) {
    font-size: 20px;
    margin-bottom: 20px;
  }

  @include mq(desktop) {
    font-size: 20px;
    margin-bottom: 20px;
  }
}

.section {
  display: flex;
  justify-content: center;
  margin-bottom: 100px;
  // width: 580px;
}

.loading-spiner {
  margin-right: auto;
  margin-left: auto;
  width: 150px;
}

.main-section {
  padding-top: 20px;
  padding-bottom: 40px;
  @include mq(tablet) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.result-section {
  width: 310px;
  @include mq(tablet) {
    width: 1020px;
  }

  @include mq(desktop) {
    width: 1020px;
  }
}

.result-container {
  justify-content: center;
  flex-direction: column;
  display: flex;

  @include mq(tablet) {
    justify-content: center;
    display: flex;
    flex-direction: row;
    // grid-template-columns: 270px 700px; // 270 700
    // grid-column-gap: 20px;
    // grid-row-gap: 20px;
  }

  @include mq(desktop) {
    justify-content: center;
    display: flex;
    flex-direction: row;
    // grid-template-columns: 270px 700px; // 270 700
    // grid-column-gap: 20px;
    // grid-row-gap: 20px;
  }
}

.filter-container {
  width: 300px;
  display: flex;
  flex-direction: column;
  height: fit-content;
  padding-top: 41px;
  margin-bottom: 25px;
  @include mq(tablet) {
    width: 270px;
    margin-bottom: 0;
    margin-right: 20px;
  }

  @include mq(desktop) {
    margin-bottom: 0;
  }
}

.gallery {
  display: flex;
  flex-direction: column;

  @include mq(tablet) {
  }

  @include mq(desktop) {
  }
}

.select-container {
  width: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.select-container-vehicle,
.select-container-number,
.select-container-vin {
  justify-content: center;
  display: flex;
  flex-direction: column;
  width: 300px;
  margin-bottom: 50px;

  @include mq(tablet) {
    width: 580px;
  }

  @include mq(desktop) {
    width: 580px;
  }
}

button {
  min-height: 50px;
  padding: 5px 10px;
  border-radius: 5px;
  border-color: rgb(196, 196, 196);
  text-decoration: none;
  display: inline-block;
}

select {
  padding: 5px 10px;
  border-radius: 5px;
  background-color: rgb(245, 245, 245, 0.5);
  border: 1px solid rgb(196, 196, 196);
  width: 100%;
}

select:active,
select:hover,
select:focus-visible {
  padding: 5px 10px;
  border-radius: 5px;
  background-color: rgb(245, 245, 245, 0.5);
  border: 1px solid rgb(196, 196, 196);
  outline: none;
}

.drop-down-placehold {
  display: none;
}

.button-search-vin,
.button-search-number,
.button-search-vehicle {
  background-color: #231f20;
  color: #ffffff;
  font-weight: 700;
  width: 300px;
  border: 0rem solid;

  @include mq(tablet) {
    width: 580px;
  }

  @include mq(desktop) {
    width: 580px;
  }
}

.dropdown-vehicle-make-spiner,
.dropdown-vehicle-model-spiner,
.dropdown-vehicle-year-spiner {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 50px;
}

.button-cancel-container {
  margin-left: auto;
  display: flex;
  justify-items: end;
  max-width: 300px;
  @include mq(tablet) {
    max-width: 600px;
  }

  @include mq(desktop) {
    max-width: 600px;
  }
}

.parts-container {
  width: 300px;
  @include mq(tablet) {
    width: 700px;
  }

  @include mq(desktop) {
    width: 700px;
  }
}

.button-cancel-container > .button-cancel {
  background-color: transparent;
  color: #f26100;
  font-weight: 400;
  width: 100px;
  border: 0rem solid;
  text-align: end;
}

.input-number input,
.input-vin input {
  padding: 5px 10px;
  min-height: 50px;
  width: 300px;
  border: 1px solid rgb(196, 196, 196);
  border-radius: 5px;
  margin-bottom: 20px;
  @include mq(tablet) {
    width: 580px;
  }

  @include mq(desktop) {
    width: 580px;
  }
}

.drop-down-form {
  position: relative;
  width: 300px;
  min-height: 50px;
  margin-bottom: 20px;
  border-radius: 5px;
  border-color: rgb(196, 196, 196);
  display: flex;

  @include mq(tablet) {
    width: 270px;
    width: 580px;
  }

  @include mq(desktop) {
    width: 580px;
  }
}

.drop-down-filter {
  width: 300px;
  min-height: 50px;
  margin-bottom: 20px;
  border-radius: 5px;
  border-color: rgb(196, 196, 196);
  display: flex;
  flex-direction: column;
  font-size: 20px;

  @include mq(tablet) {
    width: 270px;
  }

  @include mq(desktop) {
  }
}

.drop-down-filter select {
  min-height: 40px;
  font-size: 16px;
  margin-top: 10px;
}

.filter-container-clear {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 25px;
}

.button-filter-clear {
  background-color: transparent;
  color: #231f20;
  border-radius: 5px;
  border: 1px solid #231f20;
  font-size: 13px;
  height: 25px;
  min-height: 25px;
  width: 100px;
}

.filter-position {
  padding: 0;
  font-size: 20px;
  border: 0px solid;
}

.filter-position legend {
  margin-bottom: 10px;
}

.filter-position label {
  display: flex;
  align-items: center;
  font-size: 16px;
  margin-bottom: 10px;
}

.filter-position input {
  height: 20px;
  width: 20px;
  cursor: pointer;
  margin-right: 10px;
  border: 1px solid rgb(196, 196, 196);
  border-radius: 5px;
}

.result-top-container {
  margin-bottom: 35px;
}

.result-top-container-info {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.button-result-new-search {
  background-color: transparent;
  color: #231f20;
  border-radius: 5px;
  border: 1px solid #231f20;
  font-size: 13px;
  min-height: 20px;
  width: 120px;
  margin-left: 10px;
  padding: 5px 0px;
}

.result-top-container-info-text {
  max-width: 240px;
  font-size: 14px;
  @include mq(tablet) {
    max-width: 570px;
    font-size: 16px;
  }

  @include mq(desktop) {
    max-width: 570px;
  }
}

.result-top-line {
  border: 3px solid #f37f00;
}

.hidden {
  display: none;
}
