.part-card {
  display: flex;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 20px;
  flex-direction: column;
  @include mq(tablet) {
    flex-direction: row;
  }
  @include mq(desktop) {
    flex-direction: row;
  }
  
  // .film-card__img
  &__picture img {    
    border-radius: 5px;
    margin-bottom: 10px;
    margin-right: auto;
    margin-left: auto;
    width: 140px;
    height: 140px;
    @include mq(tablet) {
    }
    @include mq(desktop) {
    }
  }
  // .film-card__list
  &__list {
     padding: 2px;
    width: 100%;
    @include mq(tablet) { 
      padding: 10px;
    }
    @include mq(desktop) {
      padding: 10px;
    }
    &__tr {
      line-height: 1.2;
      display: flex;
      margin-bottom: 10px;
    flex-direction: column;
    @include mq(tablet) {    
      display: table-row;
      line-height: 1.5;
      margin-bottom: 0;
    }
    @include mq(desktop) {
      display: table-row;
      line-height: 1.5;
      margin-bottom: 0;
    }
    }
  }

  &__link {
    cursor: pointer;
    
    width: 200px;
    @include mq(tablet) {    
      margin-left: auto;
    }
    @include mq(desktop) {
      margin-left: auto;
    }
  }

  
}
